<template>
  <div class="login">
    <el-dialog
      title="登录"
      width="300px"
      center
      :visible.sync="this.$store.state.showLogin"
      :before-close="handleClose"
    >
      <el-form
        :model="ruleForm"
        status-icon
        :rules="rules"
        ref="ruleForm"
        class="demo-ruleForm"
      >
        <el-form-item prop="user">
          <el-input
            placeholder="请输入用户信息"
            v-model.number="ruleForm.user"
            style="width: 100%"
          ></el-input>
        </el-form-item>
        <el-form-item prop="pwd">
          <el-input
            placeholder="请输密码"
            type="password"
            v-model.number="ruleForm.pwd"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button style="width: 100%" @click="Login" type="primary"
            >登录</el-button
          >
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>
 
<script>
import { LoginApi, getCartData } from "../../http/api";
export default {
  // props: ["isLogin"],
  data() {
    // 验证用户账号
    const userNameRule = /^[a-zA-Z][a-zA-Z0-9_]{4,15}$/;
    var vrUser = (rule, value, callback) => {
      if (!userNameRule.test(value)) {
        return callback(new Error("字母开头,长度5-16之间,允许字母数字下划线"));
      } else {
        callback();
      }
    };
    // 密码以字母开头,长度在6-18之间,允许字母数字和下划线
    const passwordRule = /^[a-zA-Z]\w{5,17}$/;
    var vrPwd = (rule, value, callback) => {
      if (!passwordRule.test(value)) {
        return callback(
          new Error("密码以字母开头,长度在6-18之间,允许字母数字和下划线")
        );
      } else {
        callback();
      }
    };
    return {
      ruleForm: {
        user: "",
        pwd: "",
      },
      rules: {
        user: [{ validator: vrUser, trigger: "blur" }],
        pwd: [{ validator: vrPwd, trigger: "blur" }],
      },
    };
  },
  computed: {},
  methods: {
    // 控制登录框显示隐藏
    handleClose(done) {
      this.$store.commit("showLogin", false);
      done();
    },
    Login() {
      // 通过element自定义表单校验规则，校验用户输入的用户信息
      this.$refs["ruleForm"].validate((valid, o) => {
        //如果通过校验开始登录
        if (valid) {
          LoginApi({
            userName: this.ruleForm.user,
            password: this.ruleForm.pwd,
          })
            .then((res) => {
              if (res.code == "001") {
                localStorage.setItem("user_id", res.user.user_id);
                this.$store.commit("showLogin", false);
                localStorage.setItem("username", this.ruleForm.user);
                this.$store.commit("chengeName", this.ruleForm.user);
                console.log(this.$store.state.username);
              }
            })
            .catch((err) => {
              return Promise.reject(err);
            });
        } else {
          return false;
        }
      });
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          // alert("submit!");
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
  },
};
</script>
 
