<template>
  <div class="good banxin">
    <div class="heads flex">
      <div class="left"><img :src="src" alt="" /></div>
      <div class="right flex">
        <div class="a " v-for="(item, index) in lists" :key="index">
          <img :src="item.product_picture | srcFormat" alt="" />
           <p class="names">{{item.product_name}}</p>
           <p class="title">{{item.product_title}}</p>
           <p><span class="l">{{item.product_price}}元</span><span class="r">{{item.product_selling_price}}元</span></p>
        </div>
        <div class="last"> <router-link :to="'/allgoods'">浏览更多</router-link> </div>
      </div>
    </div>
  </div>
</template>
 
<script>
import { homeGoodsApi } from "../../http/api";
export default {
  props: ["src"],
  data() {
    return {
      lists: [],
    };
  },
  created() {
    this.getTu();
  },
  methods: {
    getTu() {
      homeGoodsApi("手机").then((res) => {
       
        const { Product } = res;
        this.lists = Product;
       
      });
    },
  },
};
</script>
 
