<template>
  <div class="tophead">
    <div class="box flex banxin">
      <ul class="text">
        <li v-if="!this.$store.state.shoppingCartData">
          <span class="text-tit" @click="isShowLogin">登录</span> |
          <span class="text-tit" @click="isShowReg">注册</span>
        </li>
        <li v-if="this.$store.state.shoppingCartData">
          <span>欢迎 {{ getUserName }}</span>
        </li>
        <li><span>我的订单</span></li>
        <li><span>我的收藏</span></li>
      </ul>
      <div class="goods-cart" @click="gotoCart">购物车</div>
    </div>
    <Login :isLogin="isLogin"></Login>
  </div>
</template>

<script>
import { getCartData } from "../http/api";
import Login from "../components/com/Login.vue";
export default {
  data() {
    return {
      // 显示登录
      isLogin: true,
      showReg: true,
    };
  },
  created() {
    this.CartData();
  },
  components: {
    Login,
  },
  methods: {
    // 显示隐藏登录框
    isShowLogin() {
      this.$store.commit("showLogin", this.isLogin);
    },
    // 显示注册框
    ShowReg() {
      console.log("点击");
      this.$store.commit("showReg", this.isShowReg);
      console.log(this.$store.state.isShoReg);
    },
    // 去购物车
    gotoCart() {
      console.log("点击了");
      this.$router.push("/car");
    },
    //显示隐藏注册框
    isShowReg() {
      console.log(this.showReg);
      this.$store.commit("showReg", this.showReg);
    },
    //获取购物车数据
    CartData() {
      var user_id = localStorage.getItem("user_id");
      getCartData({ user_id: user_id }).then((res) => {
        console.log(res);
        if (res.code == "001") {
          this.$store.commit("chengeCart", res.shoppingCartData);
          setTimeout(() => {
            console.log(this.$store.state.shoppingCartData);
          }, 3000);
        }
      });
    },
  },
  computed: {
    getUserName() {
      return localStorage.getItem("username");
    },
  },
};
</script>

<style  scoped>
.box {
  background-color: #3d3d3d;
}
.tophead {
  width: 100%;
  height: 40px;
  background-color: #3d3d3d;
  color: #b0b0b0;
  font-size: 14px;
  cursor: pointer;
}
.banxin {
  justify-content: flex-end;
  height: 40px;
}
.text {
  display: flex;
  align-items: center;
}
.text li {
  margin-left: 20px;
}
.text li span:hover {
  color: #fff;
}
.goods-cart {
  width: 120px;
  height: 40px;
  text-align: center;
  line-height: 40px;
  background-color: #424242;
  margin-left: 20px;
}
.goods-cart:hover {
  background-color: #fff;
  color: #ff6752;
}
.text-tit {
  padding: 5px;
}
</style>