import request from './request'
// import qs from 'qs'

// 封装api

// 轮播图api
export const carouselApi = () => request.post('/resources/carousel')
// 商品详情api
export const detailApi = () => request.post('/product/getDetails')
// 商品详情照片api
export const detailPicApi = () => request.post('/product/getDetailsPicture')
// 首页商品信息api
export const homeGoodsApi = (parmas) => request.post("/product/getPromoProduct", { categoryName: parmas });
// 首页热门商品api
export const hotGoodsApi = (parmas) =>
  request.post("/product/getHotProduct", { categoryName: parmas });
// 首页购物车api
export const homeCarApi = parmas => request.post('/user/shoppingCart/getShoppingCart', parmas)
// 修改购物车商品数量
export const updateCarApi = parmas => request.post('/user/shoppingCart/updateShoppingCart', parmas)
// 删除购物车商品
export const delCarApi = parmas => request.post('/user/shoppingCart/deleteShoppingCart', parmas)
// 加入订单
export const addOrder = parmas => request.post('/user/order/addOrder',parmas)
// 登录api
export const LoginApi = parmas => request.post('/users/login', parmas)
// 注册
// 验证用户是否注册
export const findUserName = (parmas) => request.post("users/findUserName",parmas);
export const RegisterApi = (parmas) => request.post("users/register",parmas);
//获取购物车数据
export const getCartData = (parmas)=>request.post(`/user/shoppingCart/getShoppingCart`,parmas)

//全部商品分类
export const GoodsflApi = () => request.post('/product/getCategory')
//商品信息
export const GoodsInfoApi = (parmas) => request.post('/product/getAllProduct', parmas)
//筛选商品
export const getGoodsInfoApi = (parmas) => request.post('/product/getProductByCategory',parmas)

//商品对应的ID
export const getGoodsxqApi = (parmas) => request.post('/product/getDetailsPicture',parmas)

//商品的描述
export const getGoodsmsApi = (parmas) => request.post('/product/getDetails',parmas)
//加入购物车
export const getAddCarApi = (parmass) => request.post('/user/shoppingCart/addShoppingCart',parmass)