<template>
  <div class="home">
    <!-- 轮播 -->
    <div class="banner banxin">
      <el-carousel indicator-position="outside">
        <el-carousel-item v-for="(item, index) in banner" :key="index">
          <h3><img :src="item.imgPath | srcFormat" alt="" /></h3>
        </el-carousel-item>
      </el-carousel>
    </div>
  <div class="box">
  <ListHead title="手机" :list=list></ListHead>
  <!-- 手机商品 -->
  <Good :src=ph></Good>
  <ListHead title="家电" :list=list1></ListHead>
  <!-- 家电商品 -->
  <GoodList :jia=jia :dian=dian :lists=lists></GoodList>
  <ListHead title="配件" :list=list2></ListHead>
  <!-- 配件商品 -->
  <GoodList :jia=pei :dian=jian :lists="peijian"></GoodList>
  </div>
  </div>
</template>

<script>

import { carouselApi,hotGoodsApi } from "../http/api";
import ListHead from "../components/Home/ListHead.vue"
import Good from "../components/Home/Good.vue"
import GoodList from "../components/Home/GoodList.vue"

import ph from "../assets/image/phone.png"
import jia from "../assets/image/appliance-promo1.png"
import dian from "../assets/image/appliance-promo2.png"
import pei from "../assets/image/accessory-promo1.png"
import jian from "../assets/image/accessory-promo2.png"
export default {
  name: "Home",
  data() {
    return {
      banner: [],
      // 商品头部
      list:[],
      list1:["热门","电视影音"],
      list2:["热门","保护套","充电器"],
      // 图片变量
      ph,jia,dian,pei,jian,
      // 家电商品
        lists:[],
        // 配件
        peijian:[]
    };
  },
  components: {
   ListHead ,
   Good,
   GoodList
  },
  created() {
    this.getBanner()
    this.getDate()
    this.getPei()
  },
  methods: {
    // 轮播
    getBanner() {
      carouselApi().then((res) => {
        const { carousel } = res;
        this.banner = carousel;
        // console.log(this.banner);
      });
    },
     //   家电请求
      getDate(){
          hotGoodsApi(["电视机", "空调", "洗衣机"]).then((res) => {
      const { Product } = res;
        this.lists = Product;
        // console.log(this.lists);
    });
      },
      // 配件
      getPei(){
        hotGoodsApi( ["保护套", "保护膜", "充电器", "充电宝"]).then(res=>{
           const { Product } = res;
        this.peijian = Product;
     
        })
      }
  },
};
</script>
<style>
.banner{
  padding-top: 20px;
  width: 1225px;
  height: 460px;
}
.banner h3 img{
      width: 100%;
      height: 100%;
    }
.banner .el-carousel__container{
  height: 460px;
}
.box{
  background-color: #f5f5f5;
}
</style>
