import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
  },
  {
    path: "/about",
    name: "About",
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/About.vue"),
  },
  {
    path: "/allgoods",
    name: "AllGoods",
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/AllGoods.vue"),
  },
  {
    path: "/goodsdetail",
    name: "GoodsDetail",
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/GoodsDetail.vue"),
  },
  {
    path: "/car",
    name: "Car",
    component: () => import(/* webpackChunkName: "about" */ "../views/Car.vue"),
  },
  {
    path: "/login",
    name: "Login",
    component: () => import(/* webpackChunkName: "about" */ "../components/com/Login.vue"),
  },
];

const router = new VueRouter({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes
})
router.beforeEach((to, from, next) => {
  // 正则表达式
  var url = /car/
  if (url.test(to.path)) {
    var token = localStorage.getItem('user_id')
    if (!token) {
      Vue.prototype.$message({
        message: "请先登录",
        type: "warning",
      });
      return
    }
  }
  next()
})
export default router
