 <template>
  <div class="login">
    <el-dialog
      title="注册"
      width="300px"
      center
      :visible.sync="this.$store.state.showReg"
      :before-close="handleClose"
    >
      <el-form
        :model="ruleForm"
        status-icon
        :rules="rules"
        ref="ruleForm"
        class="demo-ruleForm"
      >
        <el-form-item prop="user">
          <el-input
            placeholder="请输入用户信息"
            v-model.number="ruleForm.user"
            style="width: 100%"
          ></el-input>
        </el-form-item>
        <el-form-item prop="pwd">
          <el-input
            placeholder="请输密码"
            type="password"
            v-model.number="ruleForm.pwd"
          ></el-input>
        </el-form-item>
        <el-form-item prop="checkPass">
          <el-input
            type="password"
            placeholder="请再次输入密码"
            v-model="ruleForm.checkPass"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button style="width: 100%" @click="reg" type="primary"
            >注册</el-button
          >
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>
<script>
import { findUserName, RegisterApi } from "../../http/api";
export default {
  data() {
    // 验证用户账号
    const userNameRule = /^[a-zA-Z][a-zA-Z0-9_]{4,15}$/;
    var vrUser = (rule, value, callback) => {
      console.log(value);
      findUserName({
        userName: this.ruleForm.user,
      }).then((res) => {
        if (res.code == "002" || res.code === "004") {
          return callback(new Error(res.msg));
        } else if (!userNameRule.test(value)) {
          return callback(
            new Error("字母开头,长度5-16之间,允许字母数字下划线")
          );
        } else {
          callback();
        }
      });
    };
    // 密码以字母开头,长度在6-18之间,允许字母数字和下划线
    const passwordRule = /^[a-zA-Z]\w{5,17}$/;
    var vrPwd = (rule, value, callback) => {
      if (!passwordRule.test(value)) {
        return callback(
          new Error("密码以字母开头,长度在6-18之间,允许字母数字和下划线")
        );
      } else {
        if (this.ruleForm.checkPass !== "") {
          this.$refs.ruleForm.validateField("checkPass");
        }
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请再次输入密码"));
      } else if (value !== this.ruleForm.pwd) {
        callback(new Error("两次输入密码不一致!"));
      } else {
        callback();
      }
    };
    return {
      ruleForm: {
        user: "",
        pwd: "",
        checkPass: "",
      },
      rules: {
        user: [{ validator: vrUser, trigger: "blur" }],
        pwd: [{ validator: vrPwd, trigger: "blur" }],
        checkPass: [{ validator: validatePass2, trigger: "blur" }],
      },
    };
  },
  computed: {},
  methods: {
    // 控制登录框显示隐藏
    handleClose(done) {
      this.$store.commit("showReg", false);
      done();
    },
    // 验证表单
    reg() {
      // 通过element自定义表单校验规则，校验用户输入的用户信息
      this.$refs["ruleForm"].validate((valid, o) => {
        //如果通过校验开始登录
        if (valid) {
          // 验证是否注册
          findUserName({
            userName: this.ruleForm.user,
          })
            .then((res) => {
              if (res.code == "004") {
                this.tisp(res.msg, "warning");
              }
              if (res.code == "001") {
                // 验证是否注册成功
                RegisterApi({
                  userName: this.ruleForm.user,
                  password: this.ruleForm.pwd,
                }).then((res) => {
                  console.log(res);
                  if (res.code == "001") {
                    this.tisp(res.msg, "success");
                    this.$store.commit("showReg", false);
                    setTimeout(() => {
                      this.$store.commit("showLogin", true);
                    }, 2000);
                  }
                });
              }
            })
            .catch((err) => {
              return Promise.reject(err);
            });
        } else {
          return false;
        }
      });
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          alert("submit!");
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    //提示信息
    tisp(tit, type) {
      this.$message({
        message: tit,
        type: type,
        duration: 1000,
      });
    },
  },
};
</script>
