
import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
export default new Vuex.Store({
  state: {
    // 是否显示登录
    showLogin: false,
    // 显示或隐藏注册框
    showReg:false,
    //提示框类型
    KAlert:'success',
    // 购物车数据
    shoppingCartData:null,
    //用户名字
    username:'123'
  },
  mutations: {
    // 显示登录框
    showLogin(state, p) {
      state.showLogin = p;
    },
    // 显示注册框
    showReg(state,p) {
      
      state.showReg= p
    },
    // 获取用户名
    chengeName(state,p) {
      state.username = p
    },
    // 购物车信息
    chengeCart(state,p) {
      state.shoppingCartData = p
    }
  },
  actions: {},
  modules: {},
});
